// Fuente Principal
$fuente_principal: 'Josefin Sans', sans-serif;

// Colores Primarios
$rojoDesaturado: hsl(0, 36%, 70%);
$rojoSuave: hsl(0, 93%, 68%);

// Colores Neutral
$rojoGrisaceo: hsl(0, 6%, 24%);
$blanco: rgb(255, 255, 255);

// Gradientes
$gradiente_uno: 135deg, hsl(0, 0%, 100%), hsl(0, 100%, 98%);
$gradiente_dos: 135deg, hsl(0, 80%, 86%), hsl(0, 74%, 74%);
$gradiente_hover: 135deg, lighten(hsl(0, 80%, 86%), 10%), lighten(hsl(0, 74%, 74%), 10%);

// Tamaño de Media Queries
$telefono: 480px;
$tablet: 768px;
$desktop: 1024px;

$separacion: 5rem;

// Fuentes
$delgada: 300;
$semiRegular: 400;
$regular : 500;
$bold: 700;
$black: 900