html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  min-height: 100%;
  font-size: 1.6rem;
  font-family: "Josefin Sans", sans-serif;
  background: linear-gradient(135deg, hsl(0deg, 0%, 100%), hsl(0deg, 100%, 98%));
}

a {
  text-decoration: none;
  font-size: 1.6rem;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  text-align: center;
  font-weight: 700;
  margin: 0 0 1.6666666667rem 0;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
}

p {
  font-size: 1.6rem;
  line-height: 2rem;
}

.contenedor {
  max-height: 100vh;
}
@media (min-width: 768px) {
  .contenedor {
    display: grid;
    grid-template-columns: 2fr 1.5fr;
    grid-template-rows: 8rem auto;
    grid-template-areas: "logo imagen_hero" "contenido imagen_hero";
    background-image: url(../../../build/img/bg-pattern-desktop.svg);
  }
}

.imagen_logo {
  padding: 2rem;
}
@media (min-width: 768px) {
  .imagen_logo {
    padding: 4rem 10rem;
  }
}
.imagen_logo img {
  width: 10rem;
}
@media (min-width: 768px) {
  .imagen_logo img {
    width: 13rem;
  }
}

.imagen_hero {
  grid-area: imagen_hero;
}
@media (min-width: 768px) {
  .imagen_hero {
    max-height: 100vh;
  }
}
.imagen_hero img {
  width: 100%;
  height: 100%;
}

.contenedor_sm {
  padding: 5rem 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .contenedor_sm {
    margin: 0 auto;
    max-width: 80%;
  }
}
.contenedor_sm .titulo {
  line-height: 3.5rem;
  letter-spacing: 1rem;
  text-transform: uppercase;
  color: hsl(0deg, 6%, 24%);
}
@media (min-width: 768px) {
  .contenedor_sm .titulo {
    font-size: 6rem;
    text-align: left;
    line-height: 6rem;
    margin-top: 5rem;
    max-width: 80%;
  }
}
.contenedor_sm .titulo span {
  color: hsl(0deg, 36%, 70%);
  font-weight: 300;
}
.contenedor_sm p {
  color: hsl(0deg, 36%, 70%);
  line-height: 2.5rem;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .contenedor_sm p {
    text-align: left;
    max-width: 80%;
  }
}
.contenedor_sm .input_email {
  display: flex;
}
@media (min-width: 768px) {
  .contenedor_sm .input_email {
    max-width: 80%;
  }
}
.contenedor_sm .input_email .campo {
  display: flex;
  border: 1px solid hsl(0deg, 36%, 70%);
  align-items: center;
  justify-content: space-around;
  padding: 0rem 4rem 0rem 2rem;
  border-radius: 4rem;
  margin-right: -5rem;
  flex: 1;
}
.contenedor_sm .input_email .campo input {
  padding: 1rem 0;
  border: none;
  width: 80%;
  align-self: center;
  background-color: transparent;
}
.contenedor_sm .input_email .campo input:focus {
  outline: none;
}
.contenedor_sm .input_email .campo input::placeholder {
  color: hsl(0deg, 36%, 70%);
}
.contenedor_sm .input_email .campo img.ocultar {
  visibility: hidden;
  opacity: 0;
}
.contenedor_sm .input_email .campo.activo {
  border: 2px solid hsl(0deg, 93%, 68%);
}
.contenedor_sm .input_email .icono_flecha {
  background: linear-gradient(135deg, hsl(0deg, 80%, 86%), hsl(0deg, 74%, 74%));
  padding: 1.5rem 2.5rem;
  border-radius: 10rem;
}
.contenedor_sm .input_email .icono_flecha:hover {
  cursor: pointer;
  background: linear-gradient(135deg, #fdeded, #f4b8b8);
  box-shadow: 0 5px 10px hsl(0deg, 36%, 70%);
}
@media (min-width: 768px) {
  .contenedor_sm .input_email .icono_flecha {
    padding: 1.5rem 3.5rem;
  }
}

.alerta {
  text-align: left;
  margin-left: 2rem;
  margin-top: 1rem;
  color: hsl(0deg, 93%, 68%);
}