@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor {
    max-height: 100vh;

    @include m.tablet {
        display: grid;
        grid-template-columns: 2fr 1.5fr;
        grid-template-rows: 8rem auto;
        grid-template-areas: 'logo imagen_hero'
            'contenido imagen_hero';
        background-image: url(../../../build/img/bg-pattern-desktop.svg);
    }
}

.imagen_logo {
    padding: 2rem;

    @include m.tablet {
        padding: 4rem 10rem;
    }

    img {
        width: 10rem;

        @include m.tablet {
            width: 13rem;
        }
    }
}

.imagen_hero {
    grid-area: imagen_hero;

    @include m.tablet {
        max-height: 100vh;
    }

    img {
        width: 100%;
        height: 100%;
    }
}

.contenedor_sm {
    padding: 5rem 2rem;
    text-align: center;

    @include m.tablet {
        margin: 0 auto;
        max-width: 80%;
    }

    .titulo {
        line-height: 3.5rem;
        letter-spacing: 1rem;
        text-transform: uppercase;
        color: v.$rojoGrisaceo;

        @include m.tablet {
            font-size: 6rem;
            text-align: left;
            line-height: 6rem;
            margin-top: 5rem;
            max-width: 80%;
        }

        span {
            color: v.$rojoDesaturado;
            font-weight: v.$delgada;
        }
    }

    p {
        color: v.$rojoDesaturado;
        line-height: 2.5rem;
        margin-bottom: 3rem;

        @include m.tablet {
            text-align: left;
            max-width: 80%;

        }
    }

    .input_email {
        display: flex;

        @include m.tablet {
            max-width: 80%;
        }

        .campo {
            display: flex;
            border: 1px solid v.$rojoDesaturado;
            align-items: center;
            justify-content: space-around;
            padding: 0rem 4rem 0rem 2rem;
            border-radius: 4rem;
            margin-right: -5rem;
            flex: 1;

            input {
                padding: 1rem 0;
                border: none;
                width: 80%;
                align-self: center;
                background-color: transparent;

                &:focus {
                    outline: none;
                }

                &::placeholder {
                    color: v.$rojoDesaturado;
                }
            }

            img {

                &.ocultar {
                    visibility: hidden;
                    opacity: 0;
                }
            }

            &.activo {
                border: 2px solid v.$rojoSuave;
            }
        }

        .icono_flecha {
            background: linear-gradient(v.$gradiente_dos);
            padding: 1.5rem 2.5rem;
            border-radius: 10rem;

            &:hover {
                cursor: pointer;
                background: linear-gradient(v.$gradiente_hover);
                box-shadow: 0 5px 10px v.$rojoDesaturado;
            }

            @include m.tablet {
                padding: 1.5rem 3.5rem;
            }
        }
    }
}

.alerta {
    text-align: left;
    margin-left: 2rem;
    margin-top: 1rem;
    color: v.$rojoSuave;
}