@use 'base/variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}

*, 
*::after, 
*::before {
    box-sizing: inherit;
}

body {
    min-height: 100%;
    font-size: 1.6rem;
    font-family: v.$fuente_principal;
    background: linear-gradient(v.$gradiente_uno);
}

a {
    text-decoration: none;
    font-size: 1.6rem;
}

img {
    max-width: 100%;
    display: block;
}